.container {
  width: 900px;
  margin: 20px 20px;

}

.card {
  background-color: #fff;
  border: none;
  border-radius: 10px;
  width: 250px;
  padding: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.card h3 {
  font-size: 18px;
  margin-bottom: 10px;
}

.card p {
  font-size: 14px;
  color: #555;
  margin: 5px 0;
}

.card button {
  background-color: red;
  color: white;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.card button:hover {
  background-color: darkred;
}
